.todo-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fffbfb;
  }
  
  h1 {
    color: #000;
    font-size: 36px;
    margin-bottom: 30px;
  }
  
  .input-container {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  input[type='text'] {
    width: 300px;
    padding: 10px;
    margin-right: 10px;
    border-radius: 5px;
    border: none;
  }
  
  button {
    padding: 10px;
    border-radius: 5px;
    border: none;
    background-color: #3aa6bd;
    color: #fff;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #4a95a5;
  }
  
  .todo-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .todo-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 0px 0px 5px #ccc;
  }
  
  .todo-item span {
    flex-grow: 1;
    margin-right: 10px;
    color: #333;
  }
  
  .todo-item button {
    padding: 5px;
    border-radius: 5px;
    border: none;
    background-color: #e74c3c;
    color: #fff;
    cursor: pointer;
  }
  
  .todo-item button:hover {
    background-color: #c0392b;
  }
  